body {
  background: #ffffff;
  font-family: sans-serif;
  font-weight: 100;
  background-repeat: no-repeat;
  background: url("./images/ConcertCrowd.jpg") no-repeat center center fixed;
  background-size: cover;
  color: white;
}

input {
  margin: 10px;
  font-family: sans-serif;
  height: 20px;
  width: 27%;
  font-size: 1.1em;
  border-radius: 5px;
  padding-left: 5px;
  font-weight: 500;
  -webkit-box-shadow: 0 8px 6px -6px black;
  box-shadow: 0 6px 8px -1px black;
}

form {
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

input:focus {
  border: 3px white solid;
}

form button {
  height: 26px;
  width: 10%;
  font-size: 1.1em;
  display: block;
  -webkit-box-shadow: 0 8px 6px -6px black;
  box-shadow: 0 6px 8px -1px black;
  margin-top: 12px;
}

button:hover {
  background-color: white;
  padding-top: 0px;
  cursor: pointer;
}

select {
  margin-left: 40px;
  -webkit-box-shadow: 0 8px 6px -6px black;
  box-shadow: 0 6px 8px -1px black;
}

.input-holder {
  width: 100%;
}

.Logo {
  height: 60px;
  width: 60px;
  -webkit-animation:spin 3.5s linear infinite;
  animation:spin 3.5s linear;
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

#map-container button {
  height: 26px;
  font-size: 1.1em;
  -webkit-box-shadow: 0 8px 6px -6px black;
  box-shadow: 0 20px 9px -6px black;
  margin: 30px 30px 0 0;
}

#selectedState {
  height: 27px;
  width: 27%;
  font-size: 1.1em;
}

#main h1 {
  font-family: 'Shrikhand', cursive;
  padding: 0px;
  font-size: 3em;
  border-bottom: 1px solid #aaa;
  color: white;
  font-weight: 100;
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;;
  text-align: center;
  margin: auto;
  letter-spacing: 2px;
}

#main {
  text-align: center;
}

#venue-grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#venue-grid .results-display {
  display: block;
  width: 90%;
  height: 60px;
  font-size: 2em;
  background-color: rgba(18, 14, 14, 0.88);
  margin-top: 7px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

#no-results {
  font-size: 2.1em;
  margin-top: 15px;
  -webkit-animation: blinker 1s linear;
          animation: blinker 1s linear;
  -webkit-animation-iteration-count: 3;
          animation-iteration-count: 3;
}

@-webkit-keyframes blinker {
  50% { opacity: .2; }
}

@keyframes blinker {
  50% { opacity: .2; }
}


.results-display {
  font-size: 1.3em;
}

.results-display h3 {
  margin-top: 10px;
}

.venue-card {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 300px;
  width: 320px;
  color: black;
  border: 1px solid white;
  margin: 15px 10px;
  padding: 7px;
  font-size: 1.3em;
  background: rgba(255, 255, 255, 0.8);
  -webkit-box-shadow: 0 8px 6px -6px black;
  box-shadow: 0 6px 8px -1px black;
}

.venue-card h5, h4, h3 {
  margin-bottom: 0px;
}

.venue-card h3, h5 {
  margin-top: 0;
}

.venue-card h4 {
  margin-top: 5px;
  font-size: .85em;
}

.emailer {
  font-size: .8em;
}

.pa-status {
  font-size: .7em;
  margin-top: 0;
}

.containerElement {
  margin: auto;
  width: 90%;
  height: 530px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: rgba(18, 14, 14, 0.89);
}

.mapElement {
  height: 80%;
  width: 90%;
}

.website {
  font-size: .9em;
  white-space: nowrap;
  overflow: hidden !important;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  display: block;
}



